splay: inline-block; /* 1 */\n  vertical-align: baseline; /* 2 */\n}\n\n/**\n * Remove the default vertical scrollbar in IE.\n */\n\ntextarea {\n  overflow: auto;\n}\n\n/**\n * 1. Add the correct box sizing in IE 10-.\n * 2. Remove the padding in IE 10-.\n */\n\n[type=\"checkbox\"],\n[type=\"radio\"] {\n  box-sizing: border-box; /* 1 */\n  padding: 0; /* 2 */\n}\n\n/**\n * Correct the cursor style of increment and decrement buttons in Chrome.\n */\n\n[type=\"number\"]::-webkit-inner-spin-button,\n[type=\"number\"]::-webkit-outer-spin-button {\n  height: auto;\n}\n\n/**\n * 1. Correct the odd appearance in Chrome and Safari.\n * 2. Correct the outline style in Safari.\n */\n\n[type=\"search\"] {\n  -webkit-appearance: textfield; /* 1 */\n  outline-offset: -2px; /* 2 */\n}\n\n/**\n * Remove the inner padding and cancel buttons in Chrome and Safari on macOS.\n */\n\n[type=\"search\"]::-webkit-search-cancel-button,\n[type=\"search\"]::-webkit-search-decoration {\n  -webkit-appearance: none;\n}\n\n/**\n * 1. Correct the inability to style clickable types in iOS and Safari.\n * 2. Change font properties to `inherit` in Safari.\n */\n\n::-webkit-file-upload-button {\n  -webkit-appearance: button; /* 1 */\n  font: inherit; /* 2 */\n}\n\n/* Interactive\n   ========================================================================== */\n\n/*\n * Add the correct display in IE 9-.\n * 1. Add the correct display in Edge, IE, and Firefox.\n */\n\ndetails, /* 1 */\nmenu {\n  display: block;\n}\n\n/*\n * Add the correct display in all browsers.\n */\n\nsummary {\n  display: list-item;\n}\n\n/* Scripting\n   ========================================================================== */\n\n/**\n * Add the correct display in IE 9-.\n */\n\ncanvas {\n  display: inline-block;\n}\n\n/**\n * Add the correct display in IE.\n */\n\ntemplate {\n  display: none;\n}\n\n/* Hidden\n   ========================================================================== */\n\n/**\n * Add the correct display in IE 10-.\n */\n\n[hidden] {\n  display: none;\n}","/*\n** Sane Defaults\n*/\n\n@mixin media-query($device) {\n    @media screen and (min-width: $device) {\n        @content;\n    }\n}\n\n\n@mixin overlay-style($width,$height,$z-index){\n  position: absolute;\n  width: $width;\n  height: $height;\n  z-index: $z-index;\n  @content;\n}","\n// =========================\n// Button styles\n\ninput[type='button'],\ninput[type='submit'] {\n  -moz-appearance: none;\n  -webkit-appearance: none;\n}\n\nbutton, .M-btn, button.M-btn, input[type='button'], input[type='submit'], a.M-btn--md, a.M-btn{\n  display: inline-block;\n  text-decoration: none;\n  background: #fff;\n  margin-top: .25rem;\n  margin-bottom: .25rem;\n\n  outline: 0;\n  border: 1px solid #333;\n  padding: .75rem 1.25rem ;\n  border-radius: 2px;\n  font-size: 1rem;\n  cursor: pointer;\n  color: #333;\n\n  &.M-btn--sm { font-size: .75rem; padding: .5rem 1.25rem; }\n  &.M-btn--lg { font-size: 1.5rem; padding: 1rem 1.75rem; }\n  &.M-btn--block { width: 100% }\n\n  &:hover{\n    background: $light-gray;\n  }\n\n  &.M-btn--primary{\n    background: $primary-color;\n    color:#fff;\n\n    &:hover{\n      background: $light-primary;\n    }\n  }\n\n\n}\n","\n$med-screen:  750px;\n$lg-screen:   1000px;\n\n$container-content-max-width: 960px;\n\n$font-stack:  'Libre Franklin', 'Helvetica Neue', 'Helvetica', 'Calibri', sans-serif;\n$default-font-color: #111;\n\n// Color Scheme Variables\n$primary-color: rgba(58, 83, 155, 1.0);\n$primary-color-tranlucent: rgba(58, 83, 155, .22);\n$dark-primary: rgba(44, 62, 80, 1.0);\n\n$light-primary: rgba(92, 151, 191, 1.0);\n\n\n$dark-gray: #95A5A6;\n$faint-gray: rgba(238, 238, 238,1);\n$faint-gray-translucent: rgba(238, 238, 238, .5);\n$light-gray: rgba(210, 215, 211, 1);\n$light-gray-translucent: rgba(210, 215, 211, .5);\n\n$med-gray: rgba(191, 191, 191, 1);\n$med-gray-translucent: rgba(191, 191, 191, .5);\n\n$light-text-shadow : 1px 1px 1px rgba(0,0,0, .1);\n$dark-text-shadow : 1px 1px 1px rgba(0,0,0, .45);\n// Import partials from `sass_dir` (defaults to `_sass`)\n\n$code-color: #96281B;\n$highlight-color: rgba(247, 202, 24, 1.0);\n\n$success-color: #019875;\n$info-color:    #5C97BF;\n$warn-color:    #F4B350;\n$fail-color:    #E26A6A;\n\n$success-color_lightened: #34CBA8;\n$info-color_lightened:    #8FCAF2;\n$warn-color_lightened:    #FFE683;\n$fail-color_lightened: #FF9D9D;\n\n$bg-success-color: #d8f6f2;\n$bg-info-color:    #cfe7fb;\n$bg-warn-color:    #faddb0;\n$bg-fail-color:    #f3bfbf;\n\n$bg-disabled-color: $light-gray;\n$bg-disabled-opacity: .4;\n\n$bg-inverted-color: #333;\n$inverted-font-color: #fff;\n\n$txt-muted-color: $dark-gray;\n\n// Nav Variables\n$nav-height: 80;\n$nav-height-mobile: $nav-height * .75;\n$hamburger-line-color: #333;\n$hamburger-hover-color: $light-gray;\n\n$site-border-radius: 2px;\n","// =========================\n/* Body Content - container */\n\n.M-container--narrow{\n  max-width: $container-content-max-width;\n  padding: 0 5px;\n  margin: 0 auto;\n  width: 95%;\n}\n\n.M-container--full{\n  width: 100%;\n}\n","// INPUT SETTINGS\n// -------------------------\n.M-input-paddings,\ninput[type='text'],\ninput[type='email'],\ninput[type='date'],\ninput[type='password']{\n  padding: 0 .75rem;\n  height: 2.5rem;\n}\n\ninput[type='date'],\ninput[type='email'],\ninput[type='password']{\n  padding: 0 .1.25rem;\n}\n\n\n\n.M-input-defaults,\ninput[type='text'],\ninput[type='email'],\ninput[type='password'],\nselect,\ntextarea{\n  line-height: 1.5;\n  outline: 0;\n  border: $med-gray 1px solid;\n  font-size: 1rem;\n  border-radius: $site-border-radius;\n  margin-bottom: 1.25rem;\n  background: #fff;\n\n  &:focus{\n    border: 1px solid $light-primary;\n  }\n}\n\n.M-textarea {\n  width: 100%;\n  padding: .5rem .75rem;\n  &:focus{\n    border: 1px solid $light-primary;\n  }\n}\n\n\n.M-form-group{\n  display: block;\n  input[type='text'],\n  input[type='email'],\n  input[type='password'],\n  input[type='date'],\n  select{\n    